.aboutBodyContainer {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.aboutLogo {
    width: 150px;
    height: auto;
    margin-top: 30px;
    margin-bottom: 10px;
}

.aboutLogoDiv {
    align-items: center;
    width: 100%;
    text-align: center;
}

.aboutContainer {
    font-family: Oswald;
    margin-left: 30px;
    margin-right: 30px;
    font-size: 20px;
}

.aboutContainer2 {
    font-family: Oswald;
    margin-left: 30px;
    margin-right: 30px;
    font-size: 20px;
    margin-bottom: 80px;
}

.aboutContainer a {
    color: rgb(23, 41, 139);
}

.footerContainerAbout {
    text-align: center;
    justify-content: center;
    align-items: center;
    align-self: center;
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: space-around;
    font-size: 22px;
    bottom: 0;
    flex: 0 0 50px;
    margin-top: auto;
    margin-bottom: 10px;
}


@font-face {
    font-family: Oswald;
    src: url(Oswald-VariableFont_wght.ttf);
}

@media only screen and (min-width: 769px) {

    .aboutLogo {
        width: 300px;
        height: auto;
    }

    .aboutContainer {
        margin-left: 200px;
        margin-right: 200px;
        margin-bottom: 60px;
        font-size: 25px;
    }

    .aboutContainer2 {
        margin-left: 200px;
        margin-right: 200px;
        margin-bottom: 60px;
        font-size: 25px;
    }

    .aboutLogo {
        margin-bottom: 50px;
    }
}