.officeName {
    text-transform: uppercase;
    font-family: 'Fugaz One', cursive;
    font-size: 18px;
}

.officialName {
    font-family: Oswald;
    font-size: 18px;
}

.partyName {
    color: rgb(107, 114, 134);
}

.resultsContainer {
    align-items: center;
    justify-content: center;
    background-color: snow;
}

@font-face {
    font-family: Oswald;
    src: url(Oswald-VariableFont_wght.ttf);
}

.results {
    margin: 20px;
    margin-bottom: 60px;
    color: rgb(33, 36, 54);
}

.contactModal {
    height: 400px;
    width: 300px;
    font-family: Oswald;
    font-size: 30px;
    text-align: center;
    border: grey;
    border-width: 1px;
    border-radius: 15px;
    border-style: solid;
    outline: none;
    align-self: center;
    box-shadow: 8px 8px #60708d;
    justify-content: space-around;
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    margin-top: 30%;
    padding: 15px;
    word-wrap: break-word;
}

.phoneNumber {
    text-decoration: none;
    color: rgb(33, 36, 54);
}

.contactButton {
    font-family: Oswald;
    font-size: 15px;
    box-shadow: 2px 2px #888888;
    border-radius: 3px;
    border: none;
    outline: none;
    background-color: rgb(229, 234, 240);
    text-transform: uppercase;
    margin-left: 10px;
    height: 35px;
    margin-top: -4px;
    margin-bottom: 10px;
}

.modalButton {
    font-size: 25px;
    width: 120px;
    background-color: white;
    box-shadow: 2px 2px #888888;
    border-radius: 5px;
    border: none;
    outline: none;
    text-transform: uppercase;
    font-family: Oswald;
}

button:hover {
    cursor: pointer;
    background-color: rgb(189, 198, 207);
}

.nameAndContact {
    display: flex;
    justify-content: space-between;
    margin: 5px;
    margin-bottom: 8px;
}

.contactEmail {
    font-size: 25px;
    margin-top: 15px;
}

.contactUrl {
    font-size: 23px;
    margin-top: 15px;
    margin-bottom: 10px;
}

@media only screen and (min-width: 769px) {

    .results {
        padding-left: 60px;
        padding-right: 60px;
    }
}


@media only screen and (min-width: 1025px) {

    .contactModal {
        margin-top: 170px;
        margin-left: auto;
        margin-right: auto;
        width: 500px;
    }

    .results {
        margin-left: 270px;
        margin-right: 300px;
        margin-top: 50px;
    }

    .contactButton {
        width: 100px;
        font-size: 15px;
        height: 40px;
        text-transform: uppercase;
        margin-top: -5px;
    }

    .officeName {
        font-size: 20px;
    }

    .officialName {
        font-size: 20px;
    }

    .nameAndContact {
        height: 50px;
    }

    .noteAndFooter {
        text-align: center;
    }
}